@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/************ APP ************/

* {
  box-sizing: border-box;
  font-family: Montserrat, sans-serif;
}

html,
body {
  padding: 0;
  margin: 0;
}

html {
  background-color: #fff;
}

h1 {
  font-size: 3rem;
}

/************ HEADER ************/

.Header {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background-color: #adf0d7;
  height: 80px;
}

.Header a {
  color: #4e5052;
  text-decoration: none;
  margin: 0;
}

.adopt-link {
  float: right;
  margin-top: 10px;
}

/************ ADOPTION PAGE ************/

.adoption-container {
  padding: 2rem;
  display: flex;
  flex-flow: column;
  max-width: 80%;
  margin: 2rem auto;
}

.adoption-container h1,
.adoption-container h2 {
  text-align: center;
  color: #4e5052;
}

.adoption-container .pet-details-container h2 {
  color: #4e5052;
  text-align: left;
}

.pet-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.pet-list .pet-container {
  margin: 1rem calc(1rem + 2vw);
  background-color: #adf0d7;
  border-radius: 5px;
  display: flex;
  flex-flow: column;
  max-width: 40vh;
  justify-content: space-between;
}

.pet-list .pet-container h1 {
  color: #4e5052;
  margin: 0.3rem;
}

/************ PEOPLE LIST ************/

.people-container {
  margin: 0 calc(1rem + 2vw);
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
}
.people-container li {
  list-style: none;
  padding: 0.25rem;
  color: #4e5052;
  font-size: 1.3rem;
  text-transform: capitalize;
  font-weight: bold;
}

/************ PEOPLE FORM ************/

.btn {
  border: 0.5px solid #e6e8eb;
  padding: 5px;
  border-radius: 0.5em;
  background-color: #fff;
  color: #4e5052;
  box-shadow: 1px 1px 1px #666;
  font-size: 15px;
  cursor: pointer;
}

.btn:hover {
  box-shadow: 2px 2px 2px #666;
  background-color: #222;
  color: #efefef;
}

.name-input {
  margin: 10px;
  padding: 5px;
  border: 1px solid #666;
  border-radius: 0.5em;
}

/************ PET CARDS ************/

.pet-container li {
  list-style: none;
  margin: 3px;
}

.pet-img {
  height: 35vh;
  width: 40vh;
  padding: 5px;
}

.pet-details-container {
  padding: 5px;
  color: #222;
  font-size: 20px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.pet-details {
  text-align: left;
}

/************ MODAL ************/

.popup-container {
  position: relative;
}

.popup {
  position: fixed;
  border: 1px solid #efefef;
  border-radius: 0.5em;
  width: 50vw;
  height: 50vh;
  top: 10vh;
  left: 25vw;
  right: 25vw;
  bottom: 25vh;
  text-align: center;
  z-index: 10;
  background-color: #efefef;
  color: #222;
  font-size: 2rem;
  padding: 10px;
  display: flex !important;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 0 9999px rgba(0, 0, 0, 0.5);
}

.popup .btn {
  margin: 1.5rem;
  padding: 0.8rem;
  font-size: 1.5rem;
}

/************** LANDING PAGE **************/
h2.welcome {
  color: #4e5052;
  margin-top: 50px;
}
.Homepage {
  position: relative;
  text-align: center;
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.Homepage .page-container {
  display: flex;
  flex-direction: column;
  max-width: 1000px;
  width: 100%;
}

.process-container {
  padding: 2rem;
  background-color: #adf0d7;
}

